* {
  padding: 0;
  font-family: sans-serif;
  margin: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
}

.container_main {
  min-height: 20vh;
  padding-bottom: 50px;
}
nav,
button {
  text-decoration: none;
}
.container_main_section {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.section_common {
  padding-top: 25px;

  max-width: 70vmin;
}
.section_common p {
  font-size: 2.7vmin;
  line-height: 1.5;

  text-align: justify;
}

.section_commonn {
  padding-top: 20px;
}

.section_common_iimage {
  width: 70vmin;
  border-radius: 5px;
min-height: 20vmin;
}

.container_2 {
  width: 100%;
  min-height: 140vh;
}

.container_2_main {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
}

.container_2_main_section_right {
  background-color: #f5f5f5;
  width: 78vmin;
  min-height: 60vmin;
  border-radius: 20px;
  padding: 10px 20px;
}

