  font-weight: 400;
  font-size: 2.8vmin;
}
.container_2_main_section_right_1 h6 {
  font-size: 2.3vmin;
  padding: 5px 20px;
}

.container_2_main_section_right_1 button {
 
  padding: 2vmin 15vmin;
  border-radius: 10px;
  border: none;
  color: #f5f5f5;
  font-size: 1.9vmin;
 
  cursor: pointer;
  background-color: #07376e;
}
.search-card_button {
  display: flex;

  justify-content: center;
  padding-top: 1vmin;
  
}
select {
  margin: 10px 10px;
  width: 38vmin;
  padding: 10px 20px;
  border-radius: 20px;
  border: none;
  color: #07376e;
  font-size: 2vmin;
}
spann {
  padding-right: 5.4vmin;
}
a {
  text-decoration: none;
  color: whitesmoke;
}

