  margin-top: 10px;
  background-color: #f5f5f5;
  width: 78vmin;
  min-height: 60vmin;
  border-radius: 20px;
  padding: 20px 20px;
}
.container_2_main_section_left_1 h2 {
  padding-top: 30px;
