.Contactus_main_ONE {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4vmin 10vmin;
  min-height: 65vh;
}

.Contactus_main {
  width: 110vmin;
  min-height: 40vh;
  border-radius: 6vmin;
  background-color: #e0d7d7;
  padding-bottom: 2vmin;
}

.Contactus_main_Heading {
  padding-top: 6vmin;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 4vmin;
  color: whitesmoke;
}

.Contactus_main_container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0 4vmin;
}

.input_box {
  padding: 3vmin 0px;
}
input {
  padding: 3vmin 4vmin;
  width: 80vmin;
  border: none;
  border-radius: 6vmin;
  font-size: 2.7vmin;
}
button {
  margin: 2.6vmin;
  padding: 3vmin 6vmin;
  border-radius: 20px;
  border: none;
  color: whitesmoke;
  background-color: #07376e;
}
